import React, { useState, useEffect } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import { TrendingUp, Calendar, DollarSign, Percent, Clock, Banknote, PiggyBank, BarChart, Share2 } from 'lucide-react';

const AssetWithdrawalSimulator = () => {
  // 入力状態の管理
  const [inputs, setInputs] = useState({
    initialAsset: 30000000, // 初期資産（円）: 3000万円
    returnRate1: 5.0, // リターン率1（%）: 5.0%
    returnRate2: 3.0, // リターン率2（%）: 3.0%
    startAge: 60, // 取り崩し開始年齢: 60歳
    withdrawalYears: 30, // 取り崩し年数: 30年
  });

  // 計算結果の状態
  const [results, setResults] = useState({
    monthlyWithdrawal: 0,
    chartData: [],
    endingAsset1: 0,
    endingAsset2: 0,
  });

  // 入力値が変更されたときの処理
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    // 返却率の場合は小数点第一位まで表示
    let newValue = parseFloat(value);
    if ((name === 'returnRate1' || name === 'returnRate2') && !isNaN(newValue)) {
      newValue = parseFloat(newValue.toFixed(1));
    }
    setInputs({
      ...inputs,
      [name]: newValue,
    });
  };

  // 数値フォーマット関数
  const formatCurrency = (value) => {
    return new Intl.NumberFormat('ja-JP', { style: 'currency', currency: 'JPY' }).format(value);
  };
  
  // 数値のみのフォーマット関数（円・％記号なし）
  const formatNumberOnly = (value) => {
    return new Intl.NumberFormat('ja-JP').format(value);
  };

  // ツイートポップアップを開く関数
  const handleTweetShare = (e) => {
    e.preventDefault();
    
    // ツイート文を生成
    const tweetText = `【資産取り崩しシミュレーション】
💰 初期資産：${formatNumberOnly(inputs.initialAsset)}円
📈 運用リターン：${inputs.returnRate1}%
⏳ 取り崩し期間：${inputs.withdrawalYears}年（${inputs.startAge}歳～${inputs.startAge + inputs.withdrawalYears}歳）
💵 毎月の取り崩し可能額：${formatNumberOnly(Math.round(results.monthlyWithdrawal))}円`;
    
    // ポップアップウィンドウを開く
    const width = 550;
    const height = 420;
    const left = (window.innerWidth - width) / 2;
    const top = (window.innerHeight - height) / 2;
    
    window.open(
      `https://twitter.com/intent/tweet?text=${encodeURIComponent(tweetText)}`,
      'tweetwindow',
      `width=${width},height=${height},left=${left},top=${top},toolbar=0,menubar=0,location=0,status=0`
    );
  };

  // シミュレーション計算
  useEffect(() => {
    // 毎月の取り崩し額を計算
    const calculateMonthlyWithdrawal = (initialAsset, annualReturnRate, years) => {
      const monthlyRate = annualReturnRate / 100 / 12;
      const months = years * 12;
      // 毎月同額を取り崩す場合の計算式
      if (monthlyRate === 0) {
        return initialAsset / months;
      }
      return (initialAsset * monthlyRate) / (1 - Math.pow(1 + monthlyRate, -months));
    };

    // 各パターンの資産推移をシミュレーション
    const simulateAssetTransition = (initialAsset, annualReturnRate, monthlyWithdrawal, years) => {
      const monthlyRate = annualReturnRate / 100 / 12;
      const months = years * 12;
      const result = [];
      let currentAsset = initialAsset;

      for (let i = 0; i <= months; i++) {
        result.push(currentAsset);
        // 資産を成長させて、取り崩し額を引く
        currentAsset = currentAsset * (1 + monthlyRate) - monthlyWithdrawal;
        // 資産がマイナスになった場合は0にする
        if (currentAsset < 0) currentAsset = 0;
      }

      return result;
    };

    // 月額取り崩し額（パターン1を優先、ない場合はパターン2を使用）
    const calculationRate = inputs.returnRate1 || inputs.returnRate2;
    const monthlyWithdrawal = calculateMonthlyWithdrawal(
      inputs.initialAsset,
      calculationRate,
      inputs.withdrawalYears
    );

    // パターン1の資産推移
    const assetTransition1 = simulateAssetTransition(
      inputs.initialAsset,
      inputs.returnRate1,
      monthlyWithdrawal,
      inputs.withdrawalYears
    );

    // パターン2の資産推移
    const assetTransition2 = simulateAssetTransition(
      inputs.initialAsset,
      inputs.returnRate2,
      monthlyWithdrawal,
      inputs.withdrawalYears
    );
    
    // 運用なし（0%リターン）の資産推移
    const assetTransition0 = simulateAssetTransition(
      inputs.initialAsset,
      0,
      monthlyWithdrawal,
      inputs.withdrawalYears
    );

    // グラフ用データの作成
    const chartData = assetTransition1.map((asset1, index) => {
      const age = inputs.startAge + index / 12;
      return {
        age, // 元の年齢データを保持
        display_age: Math.floor(age), // 表示用に切り捨てた整数値
        パターン1: Math.round(asset1),
        パターン2: Math.round(assetTransition2[index] || 0),
        運用なし: Math.round(assetTransition0[index] || 0),
      };
    });

    // 結果の更新
    setResults({
      monthlyWithdrawal,
      chartData,
      endingAsset1: assetTransition1[assetTransition1.length - 1],
      endingAsset2: assetTransition2[assetTransition2.length - 1],
    });
  }, [inputs]);

  return (
    <div className="w-full max-w-6xl mx-auto p-6 bg-gradient-to-br from-indigo-50 to-blue-50 rounded-xl shadow-lg">
      
      {/* 入力フォーム */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-8">
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-xl font-semibold mb-4 text-indigo-800 flex items-center"><PiggyBank className="mr-2" size={20} /> 資産・運用設定</h2>
          
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-1 flex items-center">
              <DollarSign className="mr-1" size={16} /> 初期資産（円）
            </label>
            <div className="relative">
              <div className="flex items-center">
                <input
                  type="number"
                  name="initialAsset"
                  value={inputs.initialAsset}
                  onChange={handleInputChange}
                  className="w-full p-2 pl-8 border border-gray-300 rounded-l-md focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                />
                <span className="bg-gray-100 p-2 border border-l-0 border-gray-300 rounded-r-md text-gray-600">円</span>
              </div>
              <Banknote className="absolute left-2 top-1/2 transform -translate-y-1/2 text-gray-400" size={16} />
            </div>
          </div>
          
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-1 flex items-center">
              <TrendingUp className="mr-1" size={16} /> 運用リターン パターン1（%）
            </label>
            <div className="relative">
              <div className="flex items-center">
                <input
                  type="number"
                  name="returnRate1"
                  value={inputs.returnRate1.toFixed(1)}
                  onChange={handleInputChange}
                  step="0.1"
                  className="w-full p-2 pl-8 border border-gray-300 rounded-l-md focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 bg-indigo-50"
                />
                <span className="bg-indigo-100 p-2 border border-l-0 border-gray-300 rounded-r-md text-gray-600">%</span>
              </div>
              <div className="absolute left-2 top-1/2 transform -translate-y-1/2 w-4 h-4 rounded-full bg-indigo-600"></div>
            </div>
          </div>
          
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-1 flex items-center">
              <TrendingUp className="mr-1" size={16} /> 運用リターン パターン2（%）
            </label>
            <div className="relative">
              <div className="flex items-center">
                <input
                  type="number"
                  name="returnRate2"
                  value={inputs.returnRate2.toFixed(1)}
                  onChange={handleInputChange}
                  step="0.1"
                  className="w-full p-2 pl-8 border border-gray-300 rounded-l-md focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 bg-cyan-50"
                />
                <span className="bg-cyan-100 p-2 border border-l-0 border-gray-300 rounded-r-md text-gray-600">%</span>
              </div>
              <div className="absolute left-2 top-1/2 transform -translate-y-1/2 w-4 h-4 rounded-full bg-cyan-600"></div>
            </div>
          </div>
        </div>
        
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-xl font-semibold mb-4 text-indigo-800 flex items-center"><Clock className="mr-2" size={20} /> 取り崩し設定</h2>
          
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-1 flex items-center">
              <Calendar className="mr-1" size={16} /> 取り崩し開始年齢
            </label>
            <div className="relative">
              <div className="flex items-center">
                <input
                  type="number"
                  name="startAge"
                  value={inputs.startAge}
                  onChange={handleInputChange}
                  className="w-full p-2 pl-8 border border-gray-300 rounded-l-md focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                />
                <span className="bg-gray-100 p-2 border border-l-0 border-gray-300 rounded-r-md text-gray-600">歳</span>
              </div>
            </div>
          </div>
          
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-1 flex items-center">
              <Clock className="mr-1" size={16} /> 取り崩し年数
            </label>
            <div className="relative">
              <div className="flex items-center">
                <input
                  type="number"
                  name="withdrawalYears"
                  value={inputs.withdrawalYears}
                  onChange={handleInputChange}
                  className="w-full p-2 pl-8 border border-gray-300 rounded-l-md focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                />
                <span className="bg-gray-100 p-2 border border-l-0 border-gray-300 rounded-r-md text-gray-600">年</span>
              </div>
            </div>
          </div>
          
          <div className="mt-8 p-4 bg-indigo-100 rounded-lg">
            <h3 className="text-lg font-medium text-indigo-800 mb-2 flex items-center">
              <Banknote className="mr-2" size={18} /> シミュレーション結果
            </h3>
            <p className="text-xl font-bold text-indigo-900">
              毎月の取り崩し可能額: {formatCurrency(Math.round(results.monthlyWithdrawal))}
            </p>
            <p className="text-sm text-gray-600 mt-2">
              年間: {formatCurrency(Math.round(results.monthlyWithdrawal * 12))}
            </p>
            <div className="relative mt-2">
              <p className="text-xs text-indigo-600 italic">
                ※ {inputs.returnRate1 ? `パターン1 (${inputs.returnRate1}%)` : `パターン2 (${inputs.returnRate2}%)`} で計算
                <a 
                  href="#"
                  onClick={handleTweetShare}
                  className="float-right inline-flex items-center px-3 py-1 bg-blue-500 text-white text-xs rounded-md hover:bg-blue-600 transition-colors ml-2"
                >
                  <Share2 className="mr-1" size={12} /> 結果をシェア
                </a>
              </p>
            </div>
          </div>
        </div>
      </div>
      
      {/* 結果表示エリア */}
      <div className="bg-white p-6 rounded-lg shadow-md mb-8">
        <h2 className="text-xl font-semibold mb-4 text-indigo-800 flex items-center"><BarChart className="mr-2" size={20} /> 資産推移シミュレーション</h2>
        
        <div className="h-96 mb-6">
          <ResponsiveContainer width="100%" height="100%">
            <LineChart
              data={results.chartData}
              margin={{ top: 5, right: 30, left: 40, bottom: 5 }}
            >
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis 
                dataKey="display_age" 
                label={{ value: '年齢', position: 'insideBottomRight', offset: -10 }} 
              />
              <YAxis 
                tickFormatter={(value) => `${(value / 10000).toLocaleString()}万`}
                label={{ value: '資産額（円）', angle: -90, position: 'insideLeft' }} 
              />
              <Tooltip 
                formatter={(value, name) => [`${formatCurrency(value)}`, name]}
                labelFormatter={(label) => `${Math.floor(label)}歳`}
              />
              <Legend />
              <Line
                type="monotone"
                dataKey="パターン1"
                stroke="#4f46e5"
                strokeWidth={2}
                dot={false}
                name={`パターン1 (${inputs.returnRate1}%)`}
              />
              <Line
                type="monotone"
                dataKey="パターン2"
                stroke="#06b6d4"
                strokeWidth={2}
                dot={false}
                name={`パターン2 (${inputs.returnRate2}%)`}
              />
              <Line
                type="monotone"
                dataKey="運用なし"
                stroke="#9ca3af"
                strokeWidth={2}
                strokeDasharray="4 4"
                dot={false}
                name="運用なし (0%)"
              />
            </LineChart>
          </ResponsiveContainer>
        </div>
        
        <div className="mt-4">
          <div className="p-4 bg-indigo-50 rounded-lg text-center">
            <h3 className="text-lg font-medium text-indigo-800">
              取り崩し期間：{inputs.withdrawalYears}年（{inputs.startAge}歳〜{inputs.startAge + inputs.withdrawalYears}歳）
            </h3>
          </div>
        </div>
      </div>
      
      {/* 解説セクション */}
      <div className="bg-white p-6 rounded-lg shadow-md">
        <h2 className="text-xl font-semibold mb-4 text-indigo-800 flex items-center">シミュレーションについて</h2>
        <div className="space-y-3 text-gray-700">
          <p>このシミュレーターは、資産の取り崩しプランをビジュアル化するツールです。</p>
          <p>計算の前提:</p>
          <ul className="list-disc pl-5 space-y-1">
            <li>毎月同額を取り崩す（定額取り崩し方式）</li>
            <li>残りの資産は継続して運用される</li>
            <li>インフレや税金は考慮していない簡易モデル</li>
            <li>取り崩し額の計算はパターン1のリターンを優先（ない場合はパターン2）</li>
          </ul>
          <p className="text-sm text-gray-500 mt-4">※ 実際の運用では、市場の変動、インフレ、税金などが影響するため、専門家へのご相談をおすすめします。</p>
        </div>
      </div>
    </div>
  );
};

export default AssetWithdrawalSimulator;